import JXObject from "jx/core/JXObject";
;
/* __
* IMPORTANT : localstorage is used here for writing. This is not shared by browsers, only the browser where is create vars can retrieve them.
*/
/**
 * MemoManager allow to create memos (cf {@link Memo})
 */
export default class MemoManager extends JXObject {
    // private _classesToForceImport = [MemoCookie, MemoLocalStorage];
    constructor(dataObject) {
        super(Object.assign({}, dataObject));
    }
    /**
     * Add a new memo.
     * IMPORTANT : If LocalStorage type is used here for writing. This is not shared by browsers,
     * only the browser where is create vars can retrieve them.
     *
     * @param {string} params.id
     * @param {string} params.type - Cookie or LocalStorage
     * @param {boolean} params.autosave
     * @return {Memo}
     */
    addMemo({ id, type = "Cookie", autosave = false }) {
        //console.log("autosave", autosave)
        var cl = this.jx.db.getClass("jx/core/memos/Memo" + type);
        var memo = new cl({ jx: this.jx, id: id + this.jx.config.app.id, autosave: autosave });
        this[id] = memo;
        return memo;
    }
}
